.text-container {
  font-size: 24px;
}

.image-container {
  max-width: 100px;
  max-height: 60px;
  object-fit: fill;
}

.image-container--border {
  @extend .image-container;
  border-spacing: 1px;
  border-style: solid;
}

.content {

}

.background {

}

.background--far {
  @extend .background;
  background-color: #fffaff;
  color: black;
}

.background--close {
  @extend .background;
  background-color: yellow;
  color: black;
}

.background--match {
  @extend .background;
  background-color: #37be75;
  color: white;
}

.background--fail {
  @extend .background;
  background-color: red;
  color: white;
}
